<template>
    <div class="numbered-slider">
        <carousel :per-page="cardsAmount"
                  :navigate-to="0"
                  :mouse-drag="false"
                  :pagination-size="10"
                  paginationActiveColor="#F063B8"
                  paginationColor="#FFFFFF">

            <slide v-for="(card, index) in cards"
                   v-bind:key="card.id">
                <div class="numbered-slider__card"
                     v-bind:class="{'numbered-slider__card--is-new': card.isNew}">
                    <div>
                        <div class="numbered-slider__number">{{index + 1}}</div>
                        <h2 class="title-h2 numbered-slider__title">{{card.title}}</h2>
                        <p class="numbered-slider__description">{{card.content}}</p>
                    </div>
                    <a href="#" class="numbered-slider__link link-learn-more">Learn more</a>
                </div>
            </slide>

        </carousel>
    </div>
</template>

<script>
    import { Carousel, Slide } from 'vue-carousel';

    export default {
        name: 'NumberedSlider',
        data() {
            return {
                width: null,
                cardsAmount: 1,
                cards: [
                    {
                        title: 'First Feature',
                        content: 'Little Red Cap opened her eyes and when she saw the sunbeams dancing to and fro through the trees and how the ground was covered with beautiful flowers.',
                        url: '#',
                        isNew: false
                    },
                    {
                        title: 'Second Feature',
                        content: 'Each time she picked one she thought that she could see an even more beautiful one a little way off, and she ran after it, going further and further into the woods.',
                        url: '#',
                        isNew: false
                    },
                    {
                        title: 'Third Feature',
                        content: 'The wolf pressed the latch, and the door opened. He stepped inside, went straight to the grandmother\'s bed, and ate her up.',
                        url: '#',
                        isNew: false
                    },
                    {
                        title: 'Fourth Feature',
                        content: 'Little Red Cap had run after the flowers. After she had gathered so many that she could not carry any more, she remembered her grandmother.',
                        url: '#',
                        isNew: true
                    }
                ]
            }
        },

        methods: {
            updateWidth() {
                this.width = window.innerWidth;
                if (this.width >= 1280) {
                    this.cardsAmount = 2;
                } else {
                    this.cardsAmount = 1;
                }
            }
        },

        beforeMount() {
            this.updateWidth();
        },

        created() {
            window.addEventListener('resize', this.updateWidth);
        },

        components: {
            Carousel,
            Slide
        },
    }
</script>

<style lang="scss">
    @import "../assets/styles/mixins";

    .numbered-slider {
        background-color: #FFC84D;
        padding: 40px 0 30px;

        .VueCarousel-wrapper {
            margin: -6px auto -10px; //.numbered-slider__card spacings compensation
        }

        .VueCarousel-slide {
            justify-content: center;
            display: flex;
        }

        .VueCarousel-dot {
            transform: scale(.6);
            outline: none !important; //default outline disabling
            &--active {
                transform: scale(1);
            }
        }

        &__card {
            background: #FFF3D7;
            box-shadow: 0 10px 0 #F5BD3F;
            border-radius: 50px;
            margin: 6px 20px 10px; //6px - spacing for label, 10px - spacing for shadow
            padding: 40px 30px;
            height: 360px;
            max-width: 390px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: relative;

            &--is-new {
                &::after {
                    content: 'NEW';
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 18px;
                    line-height: 29px;
                    color: #FFFFFF;
                    background-color: #F063B8;
                    padding: 2px 15px;
                    position: absolute;
                    top: -6px;
                    box-shadow: 0 4px 0 #EFE3C5;
                    border-radius: 0 0 4px 4px;
                }
            }
        }

        &__number {
            display: none;
        }

        &__title {
            display: flex;
            color: #212353;
        }

        &__description {
            font-size: 16px;
            line-height: 26px;
            color: #4B5D68;
            margin: 0;
        }

        &__link {
            font-weight: bold;
            font-size: 16px;
            line-height: 26px;
            letter-spacing: 0.005em;
            color: #212353;
            text-decoration: none;
            display: flex;
            align-items: center;

            &::after {
                content: '';
                display: inline-block;
                width: 20px;
                height: 14px;
                background: url("../assets/img/arrow-link.svg") no-repeat center;
                background-size: contain;
                margin-left: 24px;
            }
        }

        @include breakpoint(tablet) {
            border-radius: 50px;
            padding: 100px 0 50px;

            .VueCarousel-pagination {
                text-align: left !important;
            }

            .VueCarousel-dot-container {
                margin: 30px 0 0 50px !important; //js-inline margins drop
            }

            &__card {
                margin: 6px auto 10px;
                max-width: 390px;
                padding: 50px 55px 40px 120px;
            }

            &__number {
                display: flex;
                position: absolute;
                width: 167px;
                height: 194px;
                top: 35px;
                left: -90px;
                font-style: normal;
                font-weight: bold;
                font-size: 202px;
                line-height: 323px;
                align-items: center;
                justify-content: center;
                color: #9C69E2;
                z-index: 1;
            }
        }

        @include breakpoint(laptop) {
            max-width: 1200px;
            margin: 0 auto;

            .VueCarousel-slide {
                max-width: 600px;
                &:nth-child(even){
                    padding-right: 75px;
                    justify-content: flex-end;
                }
                &:nth-child(odd) {
                    padding-left: 75px;
                    justify-content: flex-start;
                }
            }
        }
    }
</style>
