<template>
    <div class="header">
        <div class="header__container">
            <Logo />
            <div class="header__menu-container"
                 v-bind:class="{ 'header__menu-container--expanded': isMenuActive }">
                <ul class="header__menu">
                    <li class="header__menu-item"
                        v-for="menuItem in menu"
                        v-bind:key="menuItem.id"
                        v-bind:class="{
                        'header__menu-item--parent': menuItem.submenu,
                        'header__menu-item--expanded': menuItem.isSubmenuActive
                        }"
                        v-on:click="menuItem.isSubmenuActive = !menuItem.isSubmenuActive">
                    <span class="header__menu-content"
                          v-if="menuItem.submenu">
                        {{ menuItem.title}}
                        <ul class="header__submenu">
                            <li class="header__submenu-item"
                                v-for="submenuItem in menuItem.submenu"
                                v-bind:key="submenuItem.id">
                                <a class="header__submenu-content"
                                   :href="submenuItem.url"
                                   target="_blank">
                                    {{ submenuItem.title}}
                                </a>
                            </li>
                        </ul>
                    </span>
                        <a class="header__menu-content"
                           v-else :href="menuItem.url"
                           target="_blank">
                            {{menuItem.title}}
                        </a>
                    </li>
                </ul>
                <button class="button button--purple button--big">Sign Up</button>
            </div>
            <button class="header__menu-trigger trigger"
                    v-bind:class="{ 'trigger--active': isMenuActive }"
                    v-on:click="isMenuActive = !isMenuActive">
                <div class="trigger__stripe" v-for="n in 3" v-bind:key="n"></div>
            </button>
        </div>
    </div>
</template>

<script>
    import Logo from "@/components/Logo"

    export default {
        name: 'Header',
        data() {
            return {
                isMenuActive: false,
                menu: [
                    {
                        title: 'About',
                        url: '#',
                    },
                    {
                        title: 'Help',
                        submenu: [
                            {
                                title: 'User Guide',
                                url: '#'
                            },
                            {
                                title: 'Contact Support',
                                url: '#'
                            },
                        ],
                        isSubmenuActive: false
                    },
                    {
                        title: 'Features',
                        url: '#',
                    }
                ]
            }
        },
        components: {
            Logo
        }
    }
</script>

<style lang="scss">
    @import "../assets/styles/mixins";
    .header {
        background-color: #FFFFFF;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 1;
        padding: 25px 0;
        display: grid;
        grid-template-columns: repeat(32,1fr);

        &__container {
            display: flex;
            justify-content: space-between;
            grid-column: 3/-3;
            align-items: center;
            width: 100%;
            max-width: 1920px;
            margin: 0 auto;
        }

        .trigger {
            height: 39px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            background-color: transparent;
            border: none;
            outline: transparent;

            &__stripe {
                width: 36px;
                height: 3px;
                border-radius: 2px;
                background-color: #212353;
                transition: .2s;
                &:nth-child(2) {
                    margin: 7.5px 0;
                }
            }

            &--active {
                .trigger__stripe {
                    position: relative;
                    &:nth-child(1) {
                        transform: rotate(45deg);
                        top: 10px;
                    }
                    &:nth-child(2) {
                        opacity: 0;
                    }
                    &:nth-child(3) {
                        transform: rotate(-45deg);
                        top: -10px;
                    }
                }
            }
        }

        &__menu-container {
            display: none;
            position: absolute;
            top: 100%;
            width: 100%;
            left: 0;
            background-color: #FFFFFF;
            &--expanded {
                display: block;
            }
        }

        &__menu {
            margin: 0;
            padding: 0;
            list-style: none;
        }

        &__menu-item {
            font-weight: 500;
            font-size: 27px;
            line-height: 75px;
            color: #212353;
            padding: 0 32px;
            border-top: 1px solid #D6B9FF;
            cursor: pointer;
            &:last-child {
                border-bottom: 1px solid #D6B9FF;
            }

            &--parent {
                position: relative;

                .header__menu-content::after {
                    content: '';
                    background: url('../assets/img/arrow-open.svg') no-repeat center;
                    background-size: contain;
                    height: 18px;
                    width: 11px;
                    position: absolute;
                    right: 31px;
                    top: 30px;
                    transition: .2s;
                }
            }

            &--expanded {
                .header__menu-content::after {
                    transform: rotate(90deg);
                }

                .header__submenu {
                    display: block;
                    border-top: 1px solid #D6B9FF;
                }
            }
        }

        &__menu-content,
        &__menu-content:visited {
            color: #212353;
        }

        &__submenu {
            background: #F9F4FF;
            margin: 0 -32px;
            padding: 0;
            list-style: none;
            display: none;
            overflow: hidden;
            cursor: default;
        }

        &__submenu-item {
            padding: 0 62px;
            font-size: 25px;
            line-height: 69px;
            font-weight: normal;
            cursor: pointer;
        }

        &__submenu-content,
        &__submenu-content:visited {
            color: #212353;
        }

        .button {
            margin: 44px auto;
        }

        @include breakpoint(tablet) {
            &__container {
                grid-column: 2/-2;
            }
        }

        @include breakpoint(laptop) {
            &__container {
                align-items: center;
                width: 100%;
                max-width: 1920px;
            }

            .trigger {
                display: none;
            }

            &__menu-container {
                display: flex;
                position: static;
                justify-content: space-between;
                margin-left: 130px;
            }

            &__menu {
                display: flex;
            }

            &__menu-item {
                display: flex;
                align-items: center;
                font-size: 18px;
                line-height: 160%;
                border: 0;
                padding: 0;
                margin-right: 47px;
                &:last-child {
                    border: 0;
                }

                &:hover,
                &:active {
                    .header__menu-content {
                        color: #9C69E2;
                        &::after {
                            background-image: url('../assets/img/arrow-small-down-purple.svg');
                        }
                    }
                }

                &--parent {
                    display: flex;
                    align-items: center;
                    .header__menu-content {
                        padding-right: 14px;
                        &::after {
                            background-image: url('../assets/img/arrow-small-down.svg');
                            width: 10px;
                            height: 6.25px;
                            top: calc(50% - 3.12px);
                            right: 0;
                        }
                    }
                }

                &--expanded {
                    .header__menu-content {
                        color: #9C69E2;
                        &:after {
                            background-image: url('../assets/img/arrow-small-down-purple.svg');
                            transform: rotate(180deg);
                        }
                    }
                }
            }

            &__submenu {
                position: absolute;
                background: #FFFFFF;
                border: 1px solid #D6B9FF;
                border-radius: 10px;
                padding: 12px 24px;
                width: max-content;
                overflow: visible;
                margin-top: 16px;
                box-shadow: 0 5px 0 #E2CEFE;
                &::before {
                    content: '';
                    width: 18px;
                    height: 18px;
                    display: block;
                    background-color: #FFFFFF;
                    border-top: 1px solid #D6B9FF;
                    border-left: 1px solid #D6B9FF;
                    position: absolute;
                    transform: rotate(45deg);
                    top: -10px;
                    left: 58px;
                }
            }

            &__submenu-item {
                padding: 0;
                font-size: 16px;
                line-height: 35px;
            }

            &__submenu-content,
            &__submenu-content:visited {
                color: #212353;
                &:hover {
                    color: #9c69e2;
                }
            }

            .button {
                margin: 0;
            }
        }

        @include breakpoint(desktop) {
            &__container {
                grid-column: 6/-6;
            }
        }
    }
</style>
